import React, { useState, useEffect } from 'react';
import { Card, Button, Input } from 'antd';

const AdminPage = () => {
  const [images, setImages] = useState([]);
  const [token, setToken] = useState('');
  const [isAuthenticated, setIsAuthenticated] = useState(false);

  useEffect(() => {
    if (isAuthenticated) {
      fetchImages();
    }
  }, [isAuthenticated]);

  // 
  const fetchImages = () => {
    fetch('https://shogikaisetukun.com/api/images')
      .then(response => response.json())
      .then(data => setImages(data))
      .catch(error => console.error(':', error));
  };

  // 
  const handleDelete = (imageId) => {
    fetch(`https://shogikaisetukun.com/api/images/${imageId}`, {
      method: 'DELETE',
      headers: { 'Authorization': token }
    })
    .then(response => {
      if (response.ok) {
        setImages(images.filter(image => image.id !== imageId));
      } else {
        alert('');
      }
    })
    .catch(error => console.error(':', error));
  };

  return (
    <div>
      {!isAuthenticated && (
        <div>
          <Input
            type="password"
            placeholder=""
            value={token}
            onChange={e => setToken(e.target.value)}
          />
          <Button onClick={() => setIsAuthenticated(true)}></Button>
        </div>
      )}
      {isAuthenticated && images.map(image => (
        <Card key={image.id}>
          <img alt="example" src={image.url} style={{ width: '100%', height: 'auto' }} />
          <Button onClick={() => handleDelete(image.id)}></Button>
        </Card>
      ))}
    </div>
  );
};

export default AdminPage;
