import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { Card, Modal } from 'antd';
import { Layout, Menu } from 'antd';
import './RankingPage.css';

const { Header, Footer } = Layout;

const RankingPage = () => {
  const [rankedImages, setRankedImages] = useState([]);
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [modalImageUrl, setModalImageUrl] = useState('');
  const [likedImages, setLikedImages] = useState({});

  useEffect(() => {
    fetch('https://shogikaisetukun.com/api/images/ranking')
      .then(response => response.json())
      .then(data => {
        if (Array.isArray(data)) {
          setRankedImages(data.sort((a, b) => b.likes - a.likes)); // 
        } else {
          console.error(':', data);
        }
      })
      .catch(error => {
        console.error(':', error);
      });
  }, []);

  const showModal = (imageUrl) => {
    setModalImageUrl(imageUrl);
    setIsModalVisible(true);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
  };

  return (
    <Layout className="layout">
      <Header className="Header">
        <div className="logo" />
        <Menu theme="dark" mode="horizontal" defaultSelectedKeys={['4']}>
          <Menu.Item key="1">
            <Link to="/"></Link>
          </Menu.Item>
          <Menu.Item key="2">
            <Link to="/game"></Link>
          </Menu.Item>
          <Menu.Item key="3">
              <Link to="/imageboard"></Link>
            </Menu.Item>
          <Menu.Item key="4">
            <Link to="/ranking"></Link>
          </Menu.Item>
        </Menu>
      </Header>
      <div>
        <h2></h2>
        <div className="ranking-board">
          {rankedImages.map((image, index) => (
            <Card
              key={image.id}
              hoverable
              style={{ width: 240, margin: '16px' }}
              cover={<img alt={`${index + 1}`} src={image.url} onClick={() => showModal(image.url)} />}
            >
              <p>: {image.likes}</p>
            </Card>
          ))}
        </div>
      </div>
      <Modal open={isModalVisible} footer={null} onCancel={handleCancel}>
        <img alt="Enlarged" src={modalImageUrl} style={{ width: '100%' }} />
      </Modal>
      <Footer style={{ textAlign: 'center' }}> 2023 Created by yamashita twitter:@yamashita-44</Footer>
    </Layout>
  );
};

export default RankingPage;
